
.project-container {
    position: fixed;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
}

.project-popup {
    width: 60vw;
    height: auto;
    background-color: #eee;
    display: flex;
    align-items: center;
    flex-direction: column;
    color: #000;
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.5);
    border-radius: 25px;
}

.project-header {
    display: grid;
    grid-template-columns: 94% 6%;
    width: 100%;
    margin-top: 0.5rem;
}

.project-popup h1 {
    font-size: 3rem;
    text-align: center;
    padding-left: 6%;
}

.project-popup h3 {
    cursor: pointer;
    font-size: 3rem;
    color: red;
}

.project-content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    margin: 0 3rem;
    gap: 6rem;
}

.project-content img {
    width: 400px;
    border-radius: 10px;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5);
}

.project-content p {
    font-size: 1.5rem;
}

.project-icons {
    display: flex;
    justify-content: left;
    width: 100%;
    margin: 1rem 0 0 6rem;
    gap: 1rem;
}

.tool-icon {
    width: 50px;
}

.project-buttons {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    margin: 3rem 0;
}

.project-button {
    padding: 1rem 2rem;
    font-size: 1.5rem;
    border-radius: 10px;
    background-color: #000;
    color: #fff;
    cursor: pointer;
}


.hidden {
    display: none;
}

/* Media queries for screen sizes */
@media (max-width: 1100px) {
    .project-popup {
        width: 80vw;
    }
}

@media (max-width: 768px) {
    .project-popup {
        width: 90vw;
    }

    .project-header h1 {
        font-size: 2.5rem;
    }

    .project-header h3 {
        font-size: 2.5rem;
    }

    .project-content img {
        width: 300px;
    }

    .project-content p {
        font-size: 1.25rem;
    }

    .project-icons img {
        width: 35px;
    }

    .project-buttons {
        scale: 0.8;
    }
}

@media (max-width: 480px) {
    .project-popup {
        height: auto;
    }

    .project-content img {
        display: none;
    }

    .project-header {
        grid-template-columns: 90% 10%;
    }

    .project-header h1 {
        font-size: 2rem;
        margin-left: 1rem;
    }

    .project-header h3 {
        font-size: 2rem;
    }

    .project-buttons {
        scale: 0.7;
        gap: 1rem;
        margin-top: 1rem;
        margin-bottom: 2rem;
    }
}