
.navbar {
    position: fixed;
    top: 0;
    height: 80px;
    width: 100vw;
    display: flex;
    background-color: rgb(20, 20, 20);
    z-index: 1000;
}

.navbar ul{
    display: flex;
    justify-content: end;
    gap: 10rem;
    align-items: center;
    width: 100%;
    height: 100%;
    margin-right: 5rem;
    font-size: 1.25rem;
    list-style: none;
}

.logo {
    display: flex;
    align-items: center;
    margin-left: 5rem;
    width: 40%;
}

.logo img {
    height: 55px;
    clip-path: circle(40%);
    margin-right: 1rem;
}

.logo h1 {
    font-size: 1.5rem;
    width: 100%;
}

.navbar ul li {
    cursor: pointer;
}


@media (max-width: 1100px) {
    .navbar ul {
        gap: 5rem;
        margin-right: 3rem;
    }

    .logo {
        margin-left: 3rem;
    }

    .logo h1 {
        font-size: 1.25rem;
    }
}

@media (max-width: 768px) {
    .navbar ul {
        gap: 3rem;
        margin-right: 1rem;
    }

    .logo {
        margin-left: 1rem;
        width: 50%;
    }    
}

@media (max-width: 480px) {
    .navbar ul {
        display: none;
    }

    .navbar {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 65px;
    }

    .logo {
        margin: 0 1rem;
        width: 100%;
    }
}